import React, { createContext, useState, useEffect } from 'react';
import axios from 'axios';
import { url } from '../api/url';

export const UsersContext = createContext();

export const UsersProvider = ({ children }) => {
  const [users, setUsers] = useState(() => {
    //    localStorage,   
    const savedUsers = localStorage.getItem('users');
    return savedUsers ? JSON.parse(savedUsers) : [];
  });
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [isDataFetched, setIsDataFetched] = useState(false);

  //     
  useEffect(() => {
    //    ,   
    if (isDataFetched) return;

    const fetchUsers = async () => {
      setIsLoading(true);
      try {
        const response = await axios.get(url, {
          params: {
            action: 'getUsers',
          },
        });
        
        const newData = response.data;
        
        // ,   
        if (JSON.stringify(newData) !== JSON.stringify(users)) {
          setUsers(newData); //   
          localStorage.setItem('users', JSON.stringify(newData)); //   localStorage
        }

        setIsDataFetched(true);
      } catch (err) {
        setError(err);
      } finally {
        setIsLoading(false);
      }
    };

    fetchUsers();
  }, [isDataFetched, users]);

  //     
  const handleAddUser = async (userData) => {
    setIsLoading(true);
    try {
      const response = await axios.post(url, userData, {
        params: { action: 'addUser' },
        headers: { 'Content-Type': 'text/plain;charset=UTF-8' },
      });

      //        localStorage
      setUsers((prevUsers) => {
        const updatedUsers = [...prevUsers, response.data];
        localStorage.setItem('users', JSON.stringify(updatedUsers));
        return updatedUsers;
      });

      alert(' !');
      setIsDataFetched(false)
    } catch (err) {
      console.error('   :', err);
      setError(err);
    } finally {
      setIsLoading(false);
    }
  };

  //      
  return (
    <UsersContext.Provider value={{ users, isLoading, error, handleAddUser }}>
      {children}
    </UsersContext.Provider>
  );
};
